import {
  renderCategoryButtons,
  renderFilteredCards,
} from './helpers/render-functions';

import axios from 'axios';
import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';

export const api = axios.create({
  baseURL: 'https://paw-hut.b.goit.study/',
  timeout: 5000,
  headers: { accept: 'application/json' },
});

export let allPets = []; // –≤—Å—ñ —Ç–≤–∞—Ä–∏–Ω–∫–∏

export async function fetchAllPets() {
  const limit = 30;
  let page = 1;
  let allFetched = [];

  try {
    const limit = 20;
    let page = 1;
    let allFetched = [];

    while (true) {
      const requests = Array.from({ length: 5 }, (_, i) =>
        api.get('api/animals', {
          params: { page: page + i, limit },
        })
      );

      const responses = await Promise.all(requests);
      const batch = responses.flatMap(res => res.data.animals || []);
      allFetched = [...allFetched, ...batch];

      if (batch.length < limit * requests.length) break;
      page += requests.length;
    }

    allPets = allFetched;
    renderFilteredCards();
  } catch (error) {
    iziToast.error({
      title: '–ü–æ–º–∏–ª–∫–∞',
      message: '–ù–µ –≤–¥–∞–ª–æ—Å—è –∑–∞–≤–∞–Ω—Ç–∞–∂–∏—Ç–∏ —Ç–≤–∞—Ä–∏–Ω–æ–∫.',
      position: 'topRight',
    });
    console.error('Axios error:', error);
  }
}

export async function fetchCategories() {
  try {
    const response = await api.get('api/categories');
    const categories = response.data || [];
    renderCategoryButtons(['–í—Å—ñ', ...categories.map(c => c.name)]);
  } catch (error) {
    console.error('–ü–æ–º–∏–ª–∫–∞ –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è –∫–∞—Ç–µ–≥–æ—Ä—ñ–π:', error);
  }
}
